import { EventTarget,Node,find,resources, TextAsset, log } from 'cc';


var _eventTarget:EventTarget = null;

export enum EventEnum_Scene {
    Go_main_scene=          1000,
    Go_Art_scene_NoAn=      1001,
}

//綁定按鈕事件
export function common_bind_touch_start(nodeName:string,callback?: Function,target?:any):void{
    let _node:Node = find(nodeName);
    if(_node){
        _node.off(Node.EventType.TOUCH_START, callback,target);
        _node.on(Node.EventType.TOUCH_START, callback,target);
    }
    else{
        console.log("common_bind_touch_start: node not found -> "+nodeName);
    }
}

//上層自定義事件發送
export function common_getEventTarget() {
    if(_eventTarget === null) {
        _eventTarget = new EventTarget();
        console.log('new eventTarget');
    }
    return _eventTarget;
}
