import { find, UITransform,Node,RichText,AudioClip,resources,Animation} from 'cc';
import { common_getEventTarget,EventEnum_Scene,common_bind_touch_start} from './Common';


class Proc_main_scene{ 

    private page_nodes:Node[] = [];
    private strPagePath:string[] = ["Canvas/mask-001","Canvas/mask"];

    public init():void{
     
        console.log("Proc_main_scene init");

        for(let i=0;i<this.strPagePath.length;i++){
            this.page_nodes.push(find(this.strPagePath[i]));
        }
        this.switchPage(1);


        common_bind_touch_start("Canvas/mask-001/主頁設計_異界解密/異界解密/主頁設計_遊戲icon_超能學習_一般-11",
        function(){
            this.switchPage(2);
        },this);

        common_bind_touch_start("Canvas/mask/超能學習/主頁設計_遊戲icon_超能學習_一般-07",
        function(){
            this.switchPage(1);
        },this);

        common_bind_touch_start("Canvas/mask-001/主頁設計_異界解密/異界解密/主頁設計_PLAY_異界解密",
        function(){
            window.location.href = "../ccg2022/index.html";
        },this);

        common_bind_touch_start("Canvas/mask/超能學習/主頁設計_PLAY_超能學習",
        function(){
            window.location.href = "../termagame2022/index.html";
        },this);

    }

    private switchPage(iPage:number):void{
       
        for(let i=0;i<this.page_nodes.length;i++){
            if(i ==(iPage-1)){
                this.page_nodes[i].active = true;
                this.page_nodes[i].getComponent(Animation).play();
            }
            else{
                this.page_nodes[i].active = false;
            }

        }

    }


    public update():void{
        

    }
    
}



var _proc_main_scene:Proc_main_scene = null;


export function getProc_main_scene() {

    if(_proc_main_scene === null) {
        _proc_main_scene = new Proc_main_scene();
        console.log('new Proc_main_scene');
    }
    return _proc_main_scene;
} 

